$(function(){


    $(".menu").on("click", function(){

        if($(".navegacion").hasClass("hidden")){

                $(".navegacion").removeClass("hidden");

        }else{

            $(".navegacion").addClass("sacar-navegacion");
            setTimeout(()=>{
                
                
                $(".navegacion").addClass("hidden");
                $(".navegacion").removeClass("sacar-navegacion");

            }, 900);

        }

    })

    // EVENTO CLICK A LOS ENLACES

    //Cargar Pregunta Actual
    var pregActual = $(".formulario").attr("data-preg-actual");
    obtenerPregResp(pregActual);


    //// AJAX PARA CARGAR LAS PREGUNTAS
  



  


});






function obtenerPregResp(id){

    $id = parseInt(id);


    $.ajax({
        // la URL para la peticin
        url : '/teorico/controllers/PreguntasController.php',
     
        // la informacin a enviar
        // (tambin es posible utilizar una cadena de datos)
        data : { id_pregunta : id },
     
        // especifica si ser una peticin POST o GET
        type : 'POST',
     
        // el tipo de informacin que se espera de respuesta
        dataType : 'json',
     
        // cdigo a ejecutar si la peticin es satisfactoria;
        // la respuesta es pasada como argumento a la funcin
        success : function(json) {
           
            ponerPregResp(json);
        },
     
        // cdigo a ejecutar si la peticin falla;
        // son pasados como argumentos a la funcin
        // el objeto jqXHR (extensin de XMLHttpRequest), un texto con el estatus
        // // de la peticin y un texto con la descripcin del error que haya dado el servidor
        // error : function(jqXHR, status, error) {
        //     alert('Disculpe, existi un problema');
        // },
     
        // // cdigo a ejecutar sin importar si la peticin fall o no
        // complete : function(jqXHR, status) {
        //     alert('Peticin realizada');
        // }
    });



}











function   ponerPregResp(json){


    var pregunta = json.pregunta;
    var respuestas = json.respuestas;
    var correcta = json.correcta;

    var form = $(".formulario")[0];
    form.innerHTML = "";
    console.log(form);

    for(indice_respuesta in respuestas){

        let respuesta = respuestas[indice_respuesta];
        
        form.innerHTML += `

        <div class="campos">
        <label for="respuesta${indice_respuesta}" >${respuesta}</i></label>    
        <input name="respuesta"   class="hidden" id="respuesta${indice_respuesta}" type="radio">   
        </div>
        
        `;

    }

    console.log(pregunta);

    $(".pregunta p:first").text(pregunta);

    console.log()






}






